import {FC} from 'react';
import styles from './Button.module.scss';
import cn from 'clsx';
import {ButtonProps} from "../../../types/types";
import {Link} from "react-router-dom";

const Button: FC<ButtonProps> = ({link, size, id, type, children, color, classNames, ...props }) => {
    const buttonClasses = cn(styles['button'], type && styles[`button--${type}`], color && styles[`button--${color}`], classNames)
    const button = () => {
        if (link) {
            const href = props.href as string;

            return (
                <Link {...props} data-testid={id} to={href} className={buttonClasses}>
                    { children }
                </Link>
            );
        }

        return (
            <button {...props} id={id} data-testid={id} className={buttonClasses} data-size={size && size}>
                { children }
            </button>
        );
    };

    return (
        button()
    );
};

export default Button;
